import dayjs from 'dayjs';
import { dateToPicker, getColumns, getTimeOption, pickerToDate } from '../../time-picker/utils';
import BasePicker from './base-picker';
export default class DateTimePicker extends BasePicker {
    getStep() {
        return 'day';
    }
    getDisableType() {
        return 'date';
    }
    getDataSource() {
        const { validRange, format = 'YYYY-MM-DD' } = this.props;
        const oneDay = 24 * 3600 * 1000;
        const result = [];
        for (let d = validRange[0]; d.getTime() < validRange[1].getTime(); d = new Date(d.getTime() + oneDay)) {
            result.push({
                label: dayjs(d).format(format),
                value: `${d.getFullYear()},${d.getMonth()},${d.getDate()}`,
            });
        }
        const options = getTimeOption(this.timeProps);
        return [result, ...getColumns(options, this.timeProps)];
    }
    get timeProps() {
        const { showTime } = this.props;
        if (typeof showTime === 'object') {
            return showTime;
        }
        return {};
    }
    toDate(values) {
        const options = getTimeOption(this.timeProps);
        const x = values
            .shift()
            .split(',')
            .map((it) => parseInt(it));
        const d = pickerToDate(values, options);
        d.setFullYear(x[0]);
        d.setMonth(x[1]);
        d.setDate(x[2]);
        return d;
    }
    toValues(d) {
        const options = getTimeOption(this.timeProps);
        const values = [`${d.getFullYear()},${d.getMonth()},${d.getDate()}`, ...dateToPicker(d, options)];
        return values;
    }
}
