import dayjs from 'dayjs';
import { Component, createElement } from 'rax';
import Picker from '../../picker';
import { toDate } from '../../utils/date';
class BasePicker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: this.getAvailableDate(toDate(props.value || props.defaultValue || new Date())),
        };
        this.handleChange = this.handleChange.bind(this);
    }
    componentDidUpdate(prevProps) {
        if ('value' in this.props) {
            const { value: nowValue } = this.state;
            const { value, defaultValue } = this.props;
            if (value === prevProps.value) {
                return;
            }
            const newValue = this.getAvailableDate(toDate(value || defaultValue || new Date()));
            if (newValue.getTime() === nowValue.getTime()) {
                return;
            }
            this.setState({
                value: newValue,
            });
        }
    }
    filterDataSource(source, changeDate, type) {
        const { disabledDate } = this.props;
        if (!disabledDate) {
            return source;
        }
        for (let i = 0; i < source.length; i++) {
            const d = new Date();
            changeDate(d, source[i]);
            const isDisable = disabledDate(d, type);
            if (isDisable) {
                source.splice(i, 1);
                i--;
            }
        }
        return source;
    }
    getAvailableDate(d) {
        const { validRange } = this.props;
        let baseDate = d;
        if (validRange) {
            if (baseDate.getTime() < validRange[0].getTime()) {
                baseDate = validRange[0];
            }
            else if (baseDate.getTime() > validRange[1].getTime()) {
                baseDate = validRange[1];
            }
        }
        if (this.isAvailable(baseDate)) {
            return baseDate;
        }
        const dayjsObj = dayjs(baseDate);
        for (let i = 0; i < 9999; i++) {
            const a = dayjsObj.add(i, this.getStep()).toDate();
            if (this.isAvailable(a)) {
                return a;
            }
            const b = dayjsObj.add(0 - i, this.getStep()).toDate();
            if (this.isAvailable(a)) {
                return b;
            }
        }
        return baseDate;
    }
    handleChange(values) {
        const newDate = this.toDate(values);
        if (!('value' in this.props)) {
            this.setState({
                value: newDate,
            });
        }
        if (this.props.onChange) {
            this.props.onChange(newDate);
        }
    }
    render() {
        const { value } = this.props;
        const d = toDate(value) || this.state.value;
        return createElement(Picker, { value: this.toValues(d), data: this.getDataSource(), onChange: this.handleChange });
    }
    hasAvailable() {
        const { disabledDate, validRange } = this.props;
        return disabledDate || validRange;
    }
    isAvailable(d) {
        const { disabledDate, validRange } = this.props;
        if (!this.hasAvailable()) {
            return true;
        }
        if (validRange) {
            if (d.getTime() < validRange[0].getTime() || d.getTime() > validRange[1].getTime()) {
                return false;
            }
        }
        if (disabledDate) {
            return !disabledDate(d, this.getDisableType());
        }
        return true;
    }
}
export default BasePicker;
