import { CellStatusType } from './type';
export const MAX_MONTH = 11;
export const MIN_MONTH = 0;
export const MAX_DAY = 6;
export const MIN_DAY = 0;
export const NOW = new Date();
export const DEFAULT_MAX_DATE = new Date(NOW.getFullYear() + 20, 11, 31, 23, 59, 59, 999);
export const DEFAULT_MIN_DATE = new Date(2000, 0, 1, 0, 0, 0, 0);
export const getMaxMinDate = (validRange) => {
    let minDate = DEFAULT_MIN_DATE;
    let maxDate = DEFAULT_MAX_DATE;
    if (validRange && validRange.length > 0) {
        if (Object.prototype.toString.call(validRange[0]) === '[object Date]') {
            minDate = validRange[0];
        }
        if (Object.prototype.toString.call(validRange[1]) === '[object Date]') {
            maxDate = validRange[1];
        }
    }
    return [minDate, maxDate];
};
export const formatNumber = (value) => (value > 9 ? value : `0${value}`);
export const getDate = (value, maxDate, minDate) => {
    let v = value || new Date();
    if (v.getTime() > maxDate.getTime()) {
        v = cloneDate(maxDate);
    }
    if (v.getTime() < minDate.getTime()) {
        v = cloneDate(minDate);
    }
    return v;
};
export function cloneDate(d) {
    return new Date(d.getTime());
}
export const getDaysInMonth = (date) => {
    const resultDate = new Date(date.getFullYear(), date.getMonth(), 1);
    resultDate.setMonth(resultDate.getMonth() + 1);
    resultDate.setDate(resultDate.getDate() - 1);
    return resultDate.getDate();
};
export function updateYear(d, isAdd = true, maxDate, minDate) {
    let year = d.getFullYear();
    if (isAdd === true) {
        year = year + 1;
    }
    else {
        year = year - 1;
    }
    const newDate = new Date(year, d.getMonth(), 1);
    return getDate(newDate, maxDate, minDate);
}
export function updateMonth(d, isAdd = true, maxDate, minDate) {
    let month = d.getMonth();
    let year = d.getFullYear();
    if (isAdd === true) {
        month = month + 1;
        if (month > MAX_MONTH) {
            month = MIN_MONTH;
            year = year + 1;
        }
    }
    else {
        month = month - 1;
        if (month < MIN_MONTH) {
            month = MAX_MONTH;
            year = year - 1;
        }
    }
    const newDate = new Date(year, month, 1);
    const resultDate = getDate(new Date(newDate), maxDate, minDate);
    return resultDate;
}
export function getMonthList(visibleDate, locale) {
    const months = [];
    for (let month = MIN_MONTH; month <= MAX_MONTH; month += 1) {
        months.push({
            label: `${formatNumber(month + 1)}${locale.month || ''}`,
            value: new Date(visibleDate.getFullYear(), month),
        });
    }
    return months;
}
export function getDayList(local) {
    const days = [];
    for (let day = MIN_DAY; day <= MAX_DAY; day += 1) {
        days.push({
            label: local.weekdays && local.weekdays[day],
            value: day,
        });
    }
    return days;
}
export function getPreMonthDates(visibleDate, locale) {
    const days = [];
    const firstDay = new Date(visibleDate.getFullYear(), visibleDate.getMonth(), 1);
    const visibleDay = firstDay.getDay();
    if (visibleDay === 0) {
        return days;
    }
    let lastMonthDays = 0;
    let year = visibleDate.getFullYear();
    let month = visibleDate.getMonth();
    if (visibleDate.getMonth() === 0) {
        year = year - 1;
        month = MAX_MONTH;
    }
    else {
        month = month - 1;
    }
    lastMonthDays = getDaysInMonth(new Date(year, month, 1));
    const beginDate = lastMonthDays - (visibleDay - 1);
    for (let i = 0; i < visibleDay; i++) {
        days.push({
            label: `${beginDate + i}`,
            value: new Date(year, month, beginDate + i),
        });
    }
    return days;
}
export function getCurrentMonthDates(visibleDate, locale) {
    const days = [];
    const monthDays = getDaysInMonth(visibleDate);
    for (let i = 1; i <= monthDays; i++) {
        days.push({
            label: i.toString(),
            value: new Date(visibleDate.getFullYear(), visibleDate.getMonth(), i),
        });
    }
    return days;
}
export function getNextMonthDates(visibleDate, locale) {
    const days = [];
    const monthDays = getDaysInMonth(visibleDate);
    const lastDate = new Date(visibleDate.getFullYear(), visibleDate.getMonth(), monthDays);
    const lastDay = lastDate.getDay();
    let year = visibleDate.getFullYear();
    let month = visibleDate.getMonth();
    if (visibleDate.getMonth() === MAX_MONTH) {
        year = year + 1;
        month = MIN_MONTH;
    }
    else {
        month = month + 1;
    }
    for (let i = lastDay + 1; i <= MAX_DAY; i++) {
        days.push({
            label: `${i - lastDay}`,
            value: new Date(year, month, i - lastDay),
        });
    }
    return days;
}
export function getCurrentMonthList(visibleDate, local) {
    const preDates = getPreMonthDates(visibleDate, local);
    const currentDates = getCurrentMonthDates(visibleDate, local);
    const nextDates = getNextMonthDates(visibleDate, local);
    return preDates.concat(currentDates).concat(nextDates);
}
export function isTheSameDay(date1, date2) {
    if (!date1 || !date2) {
        return false;
    }
    return dayjs(date1).isSame(date2, 'day');
}
export function isTheSameMonth(date1, date2) {
    if (!date1 || !date2) {
        return false;
    }
    return dayjs(date1).isSame(date2, 'month');
}
export function isTheSameYear(date1, date2) {
    if (!date1 || !date2) {
        return false;
    }
    return date1.getFullYear() === date2.getFullYear();
}
export function isDateDisabled(cellDate, disableFunc, maxDate, minDate, view) {
    if (cellDate.getTime() > maxDate.getTime()) {
        return true;
    }
    if (cellDate.getTime() < minDate.getTime()) {
        return true;
    }
    if (disableFunc) {
        return disableFunc(cellDate, view);
    }
    return false;
}
export function isDateInRange(cellDate, selectedData) {
    if (!cellDate || !selectedData) {
        return false;
    }
    if (!Array.isArray(selectedData)) {
        return false;
    }
    if (selectedData.length < 2) {
        return false;
    }
    if (cellDate.getTime() > selectedData[selectedData.length - 1].getTime()) {
        return false;
    }
    if (cellDate.getTime() < selectedData[0].getTime()) {
        return false;
    }
    return true;
}
export function isDateSelected(cellDate, selectedData, mode, type) {
    let isSelected = null;
    if (type === 'range') {
        if (!Array.isArray(selectedData)) {
            throw new Error('Type is range, but selected is not an array');
        }
        if (isTheSameDay(cellDate, selectedData[0])) {
            return CellStatusType.begin;
        }
        if (isTheSameDay(cellDate, selectedData[selectedData.length - 1])) {
            return CellStatusType.end;
        }
    }
    else {
        if (Array.isArray(selectedData)) {
            throw new Error(`Type is ${mode}, but selected is an array`);
        }
        if (mode === 'month') {
            if (isTheSameDay(cellDate, selectedData)) {
                isSelected = CellStatusType.selected;
            }
        }
        if (mode === 'year') {
            if (isTheSameMonth(cellDate, selectedData)) {
                isSelected = CellStatusType.selected;
            }
        }
    }
    return isSelected;
}
export function getCellStatus(cellDate, selectedData, mode, type, disableFunc, maxDate, minDate, view) {
    let cellType = CellStatusType.normal;
    const isInRange = isDateInRange(cellDate, selectedData);
    if (isInRange) {
        cellType = CellStatusType.range;
    }
    const isSelected = isDateSelected(cellDate, selectedData, mode, type);
    if (isSelected) {
        cellType = isSelected;
    }
    const isDisable = isDateDisabled(cellDate, disableFunc, maxDate, minDate, view);
    if (isDisable) {
        cellType = CellStatusType.disabled;
    }
    return cellType;
}
export function getFirstDayOfMonth(d) {
    return new Date(d.getFullYear(), d.getMonth(), 1);
}
export function getWeekArray(d, firstDayOfWeek) {
    const dayArray = [];
    const daysInMonth = getDaysInMonth(d);
    const weekArray = [];
    let week = [];
    for (let i = 1; i <= daysInMonth; i++) {
        dayArray.push(new Date(d.getFullYear(), d.getMonth(), i, d.getHours(), d.getMinutes()));
    }
    const addWeek = (w) => {
        const emptyDays = 7 - w.length;
        for (let i = 0; i < emptyDays; ++i) {
            w[weekArray.length ? 'push' : 'unshift'](null);
        }
        weekArray.push(w);
    };
    dayArray.forEach((day) => {
        if (week.length > 0 && day.getDay() === firstDayOfWeek) {
            addWeek(week);
            week = [];
        }
        week.push(day);
        if (dayArray.indexOf(day) === dayArray.length - 1) {
            addWeek(week);
        }
    });
    return weekArray;
}
export function getWeekDayArray(weekdays, firstDayOfWeek) {
    const beforeArray = [];
    const afterArray = [];
    for (let i = 0; i < weekdays.length; i++) {
        if (i < firstDayOfWeek) {
            afterArray.push(weekdays[i]);
        }
        else {
            beforeArray.push(weekdays[i]);
        }
    }
    return [...beforeArray, ...afterArray];
}
export function getWeekDateArray(d, firstDayOfWeek) {
    const arr = [];
    for (let i = 0; i < 7; i++) {
        const weekDate = cloneDate(d);
        setWeek(weekDate, i, firstDayOfWeek);
        arr.push(weekDate);
    }
    return arr;
}
export function getMonthArray(d) {
    const length = 3;
    const months = [];
    let month = [];
    for (let i = 0; i < 12; i++) {
        month.push(new Date(d.getFullYear(), i, 1, d.getHours(), d.getMinutes()));
        if (month.length === length) {
            months.push(month);
            month = [];
        }
    }
    return months;
}
export function setWeek(d, day, firstDayOfWeek) {
    const week = d.getDay();
    d.setDate(d.getDate() + (day - week + firstDayOfWeek));
    return d;
}
export function cloneAsDate(d) {
    const clonedDate = cloneDate(d);
    clonedDate.setHours(0, 0, 0, 0);
    return clonedDate;
}
export function isBeforeDate(d1, d2) {
    const date1 = cloneAsDate(d1);
    const date2 = cloneAsDate(d2);
    return date1.getTime() < date2.getTime();
}
export function isAfterDate(d1, d2) {
    const date1 = cloneAsDate(d1);
    const date2 = cloneAsDate(d2);
    return date1.getTime() > date2.getTime();
}
export function isBetweenDates(dateToCheck, startDate, endDate) {
    return !isBeforeDate(dateToCheck, startDate) && !isAfterDate(dateToCheck, endDate);
}
export function isEqualDate(d1, d2) {
    return (d1 &&
        d2 &&
        d1.getFullYear() === d2.getFullYear() &&
        d1.getMonth() === d2.getMonth() &&
        d1.getDate() === d2.getDate());
}
export function normalizeValue(value, isSelectWeek, firstDayOfWeek) {
    if (!value) {
        return [];
    }
    if (!Array.isArray(value)) {
        if (isSelectWeek) {
            return [setWeek(cloneDate(value), 0, firstDayOfWeek), setWeek(cloneDate(value), 6, firstDayOfWeek)];
        }
        return [value];
    }
    return value;
}
import dayjs from 'dayjs';
export const getDefaultFormat = (type, second, use12Hours) => {
    let timeFormat = 'HH:mm';
    if (second) {
        timeFormat += ':ss';
    }
    if (use12Hours) {
        timeFormat = `${timeFormat.replace('HH', 'hh')} A`;
    }
    return {
        date: 'YYYY-MM-DD',
        year: 'YYYY',
        month: 'YYYY-MM',
        datetime: ['YYYY-MM-DD', timeFormat].join(' '),
        time: timeFormat,
    }[type];
};
export const getDateByValues = (date, values, type, second, use12Hours) => {
    const units = {
        date: 'year_month_date',
        year: 'year',
        month: 'year_month',
        datetime: `year_month_date_hour_minute${second ? '_second' : ''}`,
        time: `hour_minute${second ? '_second' : ''}`,
    }[type].split('_');
    const d = cloneDate(date);
    if (units.includes('month')) {
        d.setDate(1);
    }
    units.forEach((unit, index) => {
        const value = values[index];
        switch (unit) {
            case 'year':
                d.setFullYear(value);
                break;
            case 'month':
                d.setMonth(value);
                break;
            case 'date':
                const daysInMonth = getDaysInMonth(d);
                d.setDate(value > daysInMonth ? daysInMonth : value);
                break;
            case 'hour':
                if (use12Hours && values[values.length - 1] === 'am' && value > 11) {
                    d.setHours(value - 12);
                    break;
                }
                else if (use12Hours && values[values.length - 1] === 'pm' && value < 12) {
                    d.setHours(value + 12);
                    break;
                }
                d.setHours(value);
                break;
            case 'minute':
                d.setMinutes(value);
                break;
            case 'second':
                d.setSeconds(value);
                break;
            default:
                break;
        }
    });
    return d;
};
export const getValues = (value, type, second, use12Hours, maxDate, minDate) => {
    value = getDate(value, maxDate, minDate);
    const date = dayjs(value);
    const values = [];
    const format = getDefaultFormat(type, second, use12Hours);
    const hour = date.hour();
    if (format.indexOf('YYYY') !== -1) {
        values.push(date.year());
    }
    if (format.indexOf('MM') !== -1) {
        values.push(date.month());
    }
    if (format.indexOf('DD') !== -1) {
        values.push(date.date());
    }
    if (format.indexOf('HH') !== -1 || format.indexOf('hh') !== -1) {
        values.push(hour);
    }
    if (format.indexOf('mm') !== -1) {
        values.push(date.minute());
    }
    if (format.indexOf('ss') !== -1) {
        values.push(date.second());
    }
    if (use12Hours) {
        values.push(hour > 11 ? 'pm' : 'am');
    }
    return values;
};
export const getYears = (maxDate, minDate, locale = {}) => {
    const years = [];
    const maxYears = maxDate.getFullYear();
    const minYears = minDate.getFullYear();
    for (let year = minYears; year <= maxYears; year += 1) {
        years.push({
            label: `${year}${locale.year || ''}`,
            value: year,
        });
    }
    return years;
};
export const getMonths = (value, maxDate, minDate, locale = {}) => {
    const months = [];
    let minMonth = 0;
    let maxMonth = 11;
    if (value.getFullYear() === minDate.getFullYear()) {
        minMonth = minDate.getMonth();
    }
    if (value.getFullYear() === maxDate.getFullYear()) {
        maxMonth = maxDate.getMonth();
    }
    for (let month = minMonth; month <= maxMonth; month += 1) {
        months.push({
            label: `${formatNumber(month + 1)}${locale.month || ''}`,
            value: month,
        });
    }
    return months;
};
export const getDays = (value, maxDate, minDate, locale = {}) => {
    const days = [];
    let maxDay = getDaysInMonth(value);
    let minDay = 1;
    if (value.getFullYear() === maxDate.getFullYear() && value.getMonth() === maxDate.getMonth()) {
        maxDay = maxDate.getDate();
    }
    if (value.getFullYear() === minDate.getFullYear() && value.getMonth() === minDate.getMonth()) {
        minDay = minDate.getDate();
    }
    for (let day = minDay; day <= maxDay; day += 1) {
        days.push({
            label: `${formatNumber(day)}${locale.day || ''}`,
            value: day,
        });
    }
    return days;
};
export const getHours = (value, maxDate, minDate, locale = {}, use12Hours) => {
    let hours = [];
    let maxHour = 23;
    let minHour = 0;
    if (value.getFullYear() === maxDate.getFullYear() &&
        value.getMonth() === maxDate.getMonth() &&
        value.getDate() === maxDate.getDate()) {
        maxHour = maxDate.getHours();
    }
    if (value.getFullYear() === minDate.getFullYear() &&
        value.getMonth() === minDate.getMonth() &&
        value.getDate() === minDate.getDate()) {
        minHour = minDate.getHours();
    }
    for (let hour = minHour; hour <= maxHour; hour += 1) {
        hours.push({
            label: `${formatNumber(hour)}${locale.hour || ''}`,
            value: hour,
        });
    }
    if (use12Hours) {
        const isPM = value.getHours() > 11;
        hours = hours
            .filter((item) => {
            return (isPM && item.value > 11) || (!isPM && item.value < 12);
        })
            .map((item) => {
            if (isPM) {
                const val = item.value - 12;
                return {
                    label: `${formatNumber(val)}${locale.hour || ''}`,
                    value: item.value,
                };
            }
            return item;
        });
        if (hours.length > 0 && (hours[0].value === 0 || hours[0].value === 12)) {
            const item = hours[0];
            hours.splice(0, 1);
            hours.push(Object.assign(Object.assign({}, item), { label: `12${locale.hour || ''}` }));
        }
    }
    return hours;
};
export const getMinutes = (value, maxDate, minDate, locale = {}) => {
    const minutes = [];
    let maxMinute = 59;
    let minMinute = 0;
    if (value.getFullYear() === maxDate.getFullYear() &&
        value.getMonth() === maxDate.getMonth() &&
        value.getDate() === maxDate.getDate() &&
        value.getHours() === maxDate.getHours()) {
        maxMinute = maxDate.getMinutes();
    }
    if (value.getFullYear() === minDate.getFullYear() &&
        value.getMonth() === minDate.getMonth() &&
        value.getDate() === minDate.getDate() &&
        value.getHours() === minDate.getHours()) {
        minMinute = minDate.getMinutes();
    }
    for (let minute = minMinute; minute <= maxMinute; minute += 1) {
        minutes.push({
            label: `${formatNumber(minute)}${locale.minute || ''}`,
            value: minute,
        });
    }
    return minutes;
};
export const getSeconds = (value, maxDate, minDate, locale = {}) => {
    const seconds = [];
    let maxSecond = 59;
    let minSecond = 0;
    if (value.getFullYear() === maxDate.getFullYear() &&
        value.getMonth() === maxDate.getMonth() &&
        value.getDate() === maxDate.getDate() &&
        value.getHours() === maxDate.getHours() &&
        value.getMinutes() === maxDate.getMinutes()) {
        maxSecond = maxDate.getSeconds();
    }
    if (value.getFullYear() === minDate.getFullYear() &&
        value.getMonth() === minDate.getMonth() &&
        value.getDate() === minDate.getDate() &&
        value.getHours() === minDate.getHours() &&
        value.getMinutes() === minDate.getMinutes()) {
        minSecond = minDate.getSeconds();
    }
    for (let second = minSecond; second <= maxSecond; second += 1) {
        seconds.push({
            label: `${formatNumber(second)}${locale.second || ''}`,
            value: second,
        });
    }
    return seconds;
};
export const getAMPM = (locale) => {
    return ['am', 'pm'].map((key) => ({ label: locale[key], value: key }));
};
export const getDataSource = (value, type, second, use12Hours, maxDate, minDate, locale) => {
    value = getDate(value, maxDate, minDate);
    const format = getDefaultFormat(type, second, use12Hours);
    const data = [];
    if (format.indexOf('YYYY') !== -1) {
        data.push(getYears(maxDate, minDate, locale));
    }
    if (format.indexOf('MM') !== -1) {
        data.push(getMonths(value, maxDate, minDate, locale));
    }
    if (format.indexOf('DD') !== -1) {
        data.push(getDays(value, maxDate, minDate, locale));
    }
    if (format.indexOf('HH') !== -1 || format.indexOf('hh') !== -1) {
        data.push(getHours(value, maxDate, minDate, locale, use12Hours));
    }
    if (format.indexOf('mm') !== -1) {
        data.push(getMinutes(value, maxDate, minDate, locale));
    }
    if (format.indexOf('ss') !== -1) {
        data.push(getSeconds(value, maxDate, minDate, locale));
    }
    if (use12Hours) {
        data.push(getAMPM(locale));
    }
    return data;
};
