import { useContext } from 'rax';
import { default as Context } from './context';
export function useLocale(componentName, props = {}, defaultLocale = {}) {
    const { locale: componentLocale = {} } = props;
    const { locale = {} } = useContext(Context);
    delete props.locale;
    return Object.assign(Object.assign(Object.assign({}, defaultLocale), (locale[componentName] || {})), (componentLocale || {}));
}
