export function range(min, max) {
    const result = [];
    for (let i = min; i <= max; i++) {
        result.push(i);
    }
    return result;
}
export function rangeOptions(min, max) {
    return range(min, max).map((it) => {
        return {
            label: it.toString(),
            value: it,
        };
    });
}
export function resetMonth(d) {
    d.setMonth(1);
    d.setDate(1);
}
export function resetTime(d) {
    d.setHours(0);
    d.setMinutes(0);
    d.setSeconds(0);
    d.setMilliseconds(0);
}
