import { isMiniApp, isWeChatMiniProgram } from 'universal-env';
let systemInfo;
if (isMiniApp) {
    systemInfo = my.getSystemInfoSync();
}
else if (isWeChatMiniProgram) {
    systemInfo = wx.getSystemInfoSync();
}
export { systemInfo };
export const screenWidth = systemInfo && !isUndef(systemInfo.windowWidth) ? systemInfo.windowWidth : document.documentElement.clientWidth;
export const screenRate = screenWidth / 750;
export function removeFromArray(arr = [], val = '') {
    return arr.filter((v) => v !== val);
}
export function px2rpx(val) {
    return val / screenRate;
}
export function isValidArray(obj) {
    return Array.isArray(obj) && obj.length > 0;
}
export function isString(str) {
    return typeof str === 'string';
}
export function isUndef(obj) {
    return typeof obj === 'undefined';
}
export function isFunction(fn) {
    return typeof fn === 'function';
}
let counter = 0;
export function guid(pre = '') {
    counter += 1;
    return `${pre}${counter}`;
}
export function getImageSrc(url) {
    return {
        src: url,
        source: {
            uri: url,
        },
    };
}
export function getValueFromEvents(e) {
    if (!isUndef(e.value)) {
        return e.value;
    }
    if (!isUndef(e.target) && !isUndef(e.target.value)) {
        return e.target.value;
    }
    if (!isUndef(e.detail) && !isUndef(e.detail.value)) {
        return e.target.value;
    }
    if (!isUndef(e.originalEvent) && !isUndef(e.originalEvent.detail) && !isUndef(e.originalEvent.detail.value)) {
        return e.originalEvent.detail.value;
    }
}
