import { rangeOptions, resetMonth, resetTime } from './utils';
import YearPicker from './year-picker';
export default class MonthPicker extends YearPicker {
    getStep() {
        return 'month';
    }
    getDisableType() {
        return 'month';
    }
    getDataSource() {
        const result = super.getDataSource();
        result.push(this.filterDataSource(rangeOptions(1, 12), (d, it) => {
            resetMonth(d);
            resetTime(d);
            d.setMonth(it.value - 1);
        }, 'month'));
        return result;
    }
    toDate(values) {
        const d = super.toDate(values);
        d.setMonth(values[1] - 1);
        return d;
    }
    toValues(d) {
        const values = super.toValues(d);
        values.push(d.getMonth() + 1);
        return values;
    }
}
